import React from "react";
import "./post.css";



function Post({username, caption, imageUrl }) {
  return (
    <div className="post">
      <div className="post__header">
        <img className="perfil" src="https://media.discordapp.net/attachments/953359541968461888/969064169297039430/Eu.jpg" width={40}></img>
        <h3>{username}</h3> 
        <img src="https://media.discordapp.net/attachments/953359541968461888/969796391687843910/verificar.png" width={14}></img> 
        <img className="dot" src="https://media.discordapp.net/attachments/953359541968461888/975612110639857694/icons8-dots-loading-96.png" width={25}></img>
      </div>
      {/*  */}
      <img className="post__image" src={imageUrl} alt={caption} title={caption} />
      {/* */}
      <img className="icone" src="https://media.discordapp.net/attachments/953359541968461888/975612615772479488/icons8-gostar-50.png" width={35}></img>
      <img className="like1" src="https://media.discordapp.net/attachments/953359541968461888/972261367069413436/coracao_1.png" width={40}></img>
      <img className="like2" src="https://media.discordapp.net/attachments/953359541968461888/972261367069413436/coracao_1.png" width={40}></img>
      <img className="like3" src="https://media.discordapp.net/attachments/953359541968461888/972261367069413436/coracao_1.png" width={40}></img>
      <img className="like4" src="https://media.discordapp.net/attachments/953359541968461888/972261367069413436/coracao_1.png" width={40}></img>
      <img className="icone" src="https://media.discordapp.net/attachments/953359541968461888/975612837730873374/icons8-topico-50_2.png" width={35}></img>
      <img className="icone" src="https://media.discordapp.net/attachments/953359541968461888/975613252350377984/icons8-enviar-e-mail-48.png" width={30}></img>
      <img className="icone2" src="https://media.discordapp.net/attachments/953359541968461888/975615520093134878/icons8-contorno-de-marcadores-48.png" width={30}></img>
      <h4 className="curtidas">22.818 curtidas</h4>
      <h4 className="post__text">
        <strong>{username}</strong> {caption}
      </h4>
      <p>Ver todos os comentários</p>
      <p>Há 2 dias</p>
      <img class="feliz" src="https://media.discordapp.net/attachments/953359541968461888/969671320436613120/feliz.png" width={25} />
      <input type="text" class="coment" placeholder="Adicione um comentário..." />
      <button class="pub">Publicar</button>
      <div>
      </div>
    </div>
  );
}
export default Post;