import "./header.css"
import { Link } from "react-router-dom";
import * as React from 'react';
import HomeRoundedIcon from '@mui/icons-material/HomeRounded';
import InboxRoundedIcon from '@mui/icons-material/InboxRounded';
import AddCircleOutlineRoundedIcon from '@mui/icons-material/AddCircleOutlineRounded';
import ExploreOutlinedIcon from '@mui/icons-material/ExploreOutlined';
import FavoriteBorderOutlinedIcon from '@mui/icons-material/FavoriteBorderOutlined';
import { grey } from '@mui/material/colors';



  

export function Header ({theme}) {
  
    return (      
        <div className="app__header">
        <div className="app__headerWrapper">

            <Link to="/"><img src="https://media.discordapp.net/attachments/953359541968461888/975605730021371914/inst-removebg-preview.png"
            alt="Instagram original logo" title="Instagram original logo" width={110}/></Link>

          <input type="text" className="search" placeholder="Pesquisar"/>
          <div className="app__headerButtons">
              <HomeRoundedIcon sx={{ fontSize: 30 , color: grey [100]}}/>
              <InboxRoundedIcon sx={{ fontSize: 30, color: grey [100]}}/>
              <AddCircleOutlineRoundedIcon sx={{ fontSize: 30, color: grey [100]}}/>
              <ExploreOutlinedIcon sx={{ fontSize: 30, color: grey [100]}}/>
              <FavoriteBorderOutlinedIcon sx={{ fontSize: 30, color: grey [100]}}/>
              <Link to="/Profile"><button className="text__button"><img  className="perfil" src="https://media.discordapp.net/attachments/953359541968461888/969064169297039430/Eu.jpg" width={25} height={25}/></button></Link>
          </div>
        </div>
      </div>
    );
}

